@import "../config/globals";

body {
    background-color: $c-main-background;
    font-family: $font-main;
}

/*Это переопределит стили user agent stylesheet*/
input[type=checkbox] {
    margin: 0 0 0 0;
}

h1 {
    -webkit-margin-before: 0;
    -webkit-margin-after: 0;
    -webkit-margin-start: 0;
    -webkit-margin-end: 0;
}